import React from 'react'
import '../css/page.scss'
import text from '../translations/about.json'
import discord from '../images/bg.jpg'
import { Helmet } from 'react-helmet'


export default function About({ lang }) {
    return (
        <>
            <Helmet>
                <title>Franklin Rosales | About</title>
            </Helmet>
            <div className="page">
                <header>
                    { text[ lang.current ][ 0 ] }
                </header>
                <section className="combo">
                    <p>{ text[ lang.current ][ 1 ] }<br />{ text[ lang.current ][ 2 ] }</p>
                    <img src={ discord } alt="discord" />
                </section>
                <div className="bigtext">Pain is temporary, Glory is forever</div>
                <section>
                    <p>{ text[ lang.current ][ 3 ] }</p>
                </section>
            </div>
        </>
    )
}
